import { css } from '@emotion/react'
import React from 'react'
import { useHistory } from 'react-router-dom'
import { BiX } from 'react-icons/bi'
import palette from '../../lib/palette'

export type ContentHeaderProps = {
  title?: string
  children: React.ReactNode
}

function ContentHeader({ title, children }: ContentHeaderProps) {
  const history = useHistory()

  return (
    <div css={block}>
      <div css={header}>
        <BiX onClick={() => history.goBack()} />
        <h2 className="title">{title}</h2>
        <div />
      </div>
      <div css={contents}>{children}</div>
    </div>
  )
}

export default ContentHeader

const block = css`
  height: 100%;
  overflow: auto;
`

const header = css`
  max-width: 768px;
  width: 100%;
  height: 3rem;
  display: flex;
  align-items: center;
  background: #fff;
  border-bottom: 1px solid ${palette.primary['grey30']};
  justify-content: space-between;
  position: fixed;
  top: 0;
  padding-left: 1rem;
  padding-right: 1rem;
  z-index: 100;
  svg {
    cursor: pointer;
    font-size: 1.75rem;
  }
`
const contents = css`
  padding-top: 4rem;
  padding-left: 1rem;
  padding-right: 1rem;
`
