import { css } from '@emotion/react'
import { BiX, BiPackage, BiUser, BiCalculator } from 'react-icons/bi'
import palette from '../../lib/palette'
import { NavLink, useHistory } from 'react-router-dom'

const items = [
  {
    id: 1,
    icon: <BiPackage />,
    title: 'Goods',
    sub: [
      {
        id: 1,
        subtitle: 'Sale Goods',
        to: '',
      },
    ],
  },
  {
    id: 2,
    icon: <BiCalculator />,
    title: 'Purchased',
    sub: [
      {
        id: 1,
        subtitle: 'Purchased Goods',
        to: '',
      },
    ],
  },
  {
    id: 3,
    icon: <BiUser />,
    title: 'Contacts',
    sub: [
      {
        id: 1,
        subtitle: 'Contact List',
        to: '',
      },
      {
        id: 2,
        subtitle: 'Create Individual Contact',
        to: '/workspaces/contact/create/individual',
      },
    ],
  },
]

export type WorkspacesNavProps = {}

function WorkspacesNav({}: WorkspacesNavProps) {
  const history = useHistory()
  return (
    <div css={block}>
      <div css={header}>
        <BiX onClick={() => history.goBack()} />
        <h2>Workspaces</h2>
        <div></div>
      </div>
      <div css={contents}>
        {items.map((item) => (
          <div css={itemStyle} key={item.id}>
            <div css={mainItem}>
              <span css={iconStyle}>{item.icon}</span>
              <span css={titleStyle}>{item.title}</span>
            </div>
            {item.sub?.map((subitem) => (
              <div css={subItem} key={subitem.id}>
                <NavLink css={subTitle} to={subitem.to}>
                  {subitem.subtitle}
                </NavLink>
              </div>
            ))}
          </div>
        ))}
      </div>
    </div>
  )
}

export default WorkspacesNav

const block = css`
  display: flex;
  flex-direction: column;
  padding: 1rem;
`
const header = css`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 3rem;

  svg {
    cursor: pointer;
    font-size: 1.75rem;
  }
`
const contents = css`
  display: flex;
  flex-direction: column;
  margin-top: 1.5rem;
`
const itemStyle = css`
  width: 100%;
  margin-bottom: 1rem;
`
const mainItem = css`
  height: 3rem;
  display: flex;
  align-items: center;
`

const iconStyle = css`
  font-size: 1.5rem;
  margin-top: 5px;
  margin-right: 1rem;
`
const titleStyle = css`
  font-size: 1.125rem;
  font-weight: bold;
`

const subItem = css`
  display: flex;
  align-items: center;
  height: 2rem;
  &:hover {
    background: ${palette.primary['grey30']};
  }
`
const subTitle = css`
  position: relative;
  padding-left: 2.5rem;
  text-decoration: none;
  color: ${palette.primary['notBlack']};

  &::before {
    position: absolute;
    top: -6px;
    left: 10.5px;
    content: '';
    background: ${palette.primary['notBlack']};
    height: 2rem;
    width: 2px;
  }
`
