import client from './../client'
import { User } from './userTypes'

export async function googleSignup(accessToken: string, contact: string) {
  const { data } = await client.post<GoogleSignupResult>(
    '/user/google/signup',
    {
      accessToken,
      contact,
    }
  )
  return data
}

type GoogleSignupResult = {
  user: User
  accessToken: string
}
