import { css } from '@emotion/react'
import React from 'react'
import { BiRocket, BiStore, BiLineChart } from 'react-icons/bi'
import { NavLink } from 'react-router-dom'
import GoogleLoginButton from '../../foundations/Google/GoogleLoginButton'
import palette from '../../lib/palette'
import { useRecoilValue } from 'recoil'
import { userState } from '../../atoms/userState'
import CurrentUserInfo from '../User/CurrentUserInfo/CurrentUserInfo'

export type GlobalNavProps = {}

function GlobalNav({}: GlobalNavProps) {
  const user = useRecoilValue(userState)

  return (
    <div css={block}>
      <NavLink css={item} to="/workspaces">
        <BiRocket />
        <div>Work</div>
      </NavLink>
      <NavLink css={item} to="/" exact>
        <BiStore />
        <div>Sales</div>
      </NavLink>
      <NavLink css={item} to="/" exact>
        <BiLineChart />
        <div>Reports</div>
      </NavLink>
      {user ? <CurrentUserInfo /> : <GoogleLoginButton />}
    </div>
  )
}

export default GlobalNav

const block = css`
  display: flex;
  width: 100%;
  justify-content: space-around;
`
const item = css`
  text-decoration: none;
  cursor: pointer;
  color: ${palette.primary['grey70']};
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.75rem;

  svg {
    font-size: 1.75rem;
  }
  &:hover {
    color: ${palette.primary['accentBlue']};
  }
  &.active {
    color: ${palette.primary['accentBlue']};
  }
`
