import { useState } from 'react'
import { useHistory } from 'react-router-dom'
import { useRecoilValue } from 'recoil'
import { googleTokenState } from '../atoms/userState'
import { googleSignup } from '../lib/api/user/googleSignup'
import useAuth from './useAuthorize'

export default function useGoogleSignup() {
  const { authorize } = useAuth()
  const googleToken = useRecoilValue(googleTokenState)

  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const history = useHistory()

  const signup = async (contact: string) => {
    try {
      setLoading(true)
      const result = await googleSignup(googleToken!, contact)
      authorize(result.user)
      history.replace('/')
    } catch (e) {
      if (e.response.status === 409) {
        setError('Email already exists')
        throw e
      }
    } finally {
      setLoading(false)
    }
  }
  return {
    signup,
    loading,
    error,
  }
}
