import React, {
  Dispatch,
  SetStateAction,
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react'
import { css } from '@emotion/react'
import palette from '../../lib/palette'
import { BiX, BiChevronDown } from 'react-icons/bi'
import useOnClickOutside from 'use-onclickoutside'
/**
 * TODO:
 * 1. 단일 셀렉트 구현
 * 2. 검색 기능 구현
 * 3. input width로 글자
 */
export type SelectProps = {
  label: string
  prefix?: string
  value?: string
  setValue?: Dispatch<SetStateAction<string>>
  isClearable?: boolean
  isMulti?: boolean
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void
  options: string[]
} & React.InputHTMLAttributes<HTMLInputElement>

function Select({
  prefix,
  label,
  value,
  setValue,
  isClearable = true,
  isMulti,
  onChange,
  options,
  ...rest
}: SelectProps) {
  const [isActive, setIsActive] = useState(false)
  const [multiValue, setMultiValue] = useState<string[]>([])

  const [selectedIndex, setSelectedIndex] = useState(-1)

  const inputRef = useRef<HTMLInputElement>(null)
  const selectedRef = useRef<HTMLDivElement>(null)
  const selecteBlockRef = useRef<HTMLDivElement>(null)
  const baseRef = useRef<HTMLDivElement>(null)
  const itemRef = useRef<HTMLDivElement>(null)
  const testRef = useRef<any>({})
  const [open, setOpen] = useState(false)

  const results = options

  useEffect(() => {
    if (!open) {
      setSelectedIndex(-1)
    }
  }, [open, setSelectedIndex])

  useEffect(() => {
    if (value !== '' || value.length !== 0) {
      setOpen(true)
    }
  }, [value])

  useEffect(() => {
    if (open) {
      selecteBlockRef.current?.scrollIntoView(true)
    }
  })

  useEffect(() => {
    setIsActive(false)
    if (value !== '' || value.length !== 0) {
      return setIsActive(true)
    }
  }, [value])

  const isClear = () => {
    if (!setValue) {
      return
    }
    setValue('')
  }

  const onClickSelectedItem = () => {
    const selectedItem = results?.[selectedIndex]
    if (multiValue.includes(selectedItem)) {
      setMultiValue(multiValue.filter((el) => el !== selectedItem))
      return
    }
    setMultiValue((multiValue) => [...multiValue, selectedItem])
  }

  const onKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (!['ArrowDown', 'ArrowUp', 'Enter'].includes(e.key)) return
    e.preventDefault()
    switch (e.key) {
      case 'ArrowUp':
        if (selectedIndex - 1 === -1 || selectedIndex === -1) {
          setSelectedIndex(-1)
          return
        }

        setSelectedIndex(selectedIndex - 1)
        if (!selecteBlockRef.current || !selectedRef.current) return
        if (
          selecteBlockRef.current.scrollTop >
          selectedRef.current.clientHeight * (selectedIndex - 1)
        ) {
          if (!selecteBlockRef.current?.children[selectedIndex]) return
          selecteBlockRef.current.children[selectedIndex - 1].scrollIntoView(
            true
          )
        }
        return

      case 'ArrowDown':
        if (selectedIndex === results.length - 1) {
          return
        }
        setSelectedIndex(selectedIndex + 1)

        if (!selecteBlockRef.current || !selectedRef.current) return
        if (
          selecteBlockRef.current.clientHeight +
            selecteBlockRef.current.scrollTop <
          selectedRef.current.clientHeight * (selectedIndex + 2)
        ) {
          if (!selecteBlockRef.current?.children[selectedIndex]) return
          selecteBlockRef.current.children[selectedIndex + 1].scrollIntoView(
            false
          )
        }
        return

      case 'Enter':
        const selectedItem = results?.[selectedIndex]

        if (multiValue.includes(selectedItem)) {
          return
        }

        setMultiValue((multiValue) => [...multiValue, selectedItem])
        return
    }
  }

  const onClose: Parameters<typeof useOnClickOutside>[1] = (e) => {
    if (
      selecteBlockRef.current === e.target ||
      selecteBlockRef.current?.contains(e.target as Node) ||
      baseRef.current === e.target ||
      baseRef.current?.contains(e.target as Node)
    ) {
      return
    }
    setOpen(false)
  }

  const onRemoveItem = (item: string) => {
    if (!item) return
    setMultiValue(multiValue.filter((el) => el !== item))
  }

  useOnClickOutside(selecteBlockRef, onClose)
  const openFocus = () => {
    if (open) {
      console.log(baseRef)
      baseRef.current?.scrollIntoView(true)
    }
  }

  return (
    <div css={block}>
      <div
        ref={baseRef}
        css={base(isActive, multiValue.length > 0)}
        {...rest}
        onClick={() => {
          setOpen(!open)
          inputRef.current?.focus()
        }}
      >
        <label css={labelStyle}>{label}</label>
        <div css={multiCss(isActive, multiValue.length > 0)}>
          {multiValue?.map((item, i) => (
            <div
              className="item"
              css={multiValueCss}
              key={i}
              onClick={(e) => {
                e.stopPropagation()
                onRemoveItem(item)
              }}
              ref={(ref) => (testRef.current[item] = ref)}
            >
              {item}
              <BiX />
            </div>
          ))}

          <input
            css={multiInputCss(
              value === undefined || value.length === 0
                ? '16px'
                : `${16 * value.length + 16}px`
            )}
            ref={inputRef}
            value={value}
            autoComplete="off"
            onKeyDown={onKeyDown}
            onChange={onChange}
          />
        </div>

        {isClearable && value !== '' && (
          <div css={clear} onClick={isClear}>
            <BiX />
          </div>
        )}
        <div css={downArrow(isActive, multiValue.length > 0)}>
          <BiChevronDown />
        </div>
      </div>
      {!open || !results || results.length === 0 ? null : (
        <div css={selectBlock} ref={selecteBlockRef}>
          {results?.map((result, i) => (
            <div
              key={i}
              css={selectItem(i === selectedIndex, multiValue.includes(result))}
              ref={selectedRef}
              onMouseOver={(e) => {
                e.preventDefault()
                setSelectedIndex(i)
              }}
              onClick={onClickSelectedItem}
            >
              {result}
            </div>
          ))}
        </div>
      )}
    </div>
  )
}

export default Select

const block = css`
  width: 100%;
  position: relative;
`
const base = (isActive: boolean, multiValue: boolean) => css`
  border: 1px solid ${palette.primary['grey30']};
  border-radius: 4px;
  min-height: 3rem;
  /* position: relative; */
  display: flex;

  padding-left: 0.5rem;
  padding-right: 0.5rem;

  &:focus-within {
    border: 1px solid ${palette.primary['accentBlue']};
  }

  ${isActive &&
  css`
    label {
      height: auto;
      transform: translate(0, 4px) scale(0.75);
    }
  `}

  ${multiValue &&
  css`
    label {
      height: auto;
      transform: translate(0, 4px) scale(0.75);
    }
  `}
`
const multiCss = (isActive: boolean, multiValue: boolean) => css`
  display: flex;
  flex: 1 1 0%;
  flex-wrap: wrap;
  overflow: hidden;
  align-items: center;

  ${isActive &&
  css`
    .item {
      margin-left: 10px;
    }
    margin-top: 14px;
  `}

  ${multiValue &&
  css`
    .item {
      margin-left: 10px;
    }

    margin-top: 14px;
  `}
`
const multiValueCss = css`
  background: ${palette.primary['grey30']};
  border: 1px solid ${palette.primary['lightBlue']};
  min-height: 24px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 6px 3px;
  padding-left: 8px;
  padding-right: 6px;
  font-size: 0.75rem;
  cursor: pointer;
  svg {
    font-size: 1rem;
    margin-left: 2px;
  }
`

const multiInputCss = (size: string) => css`
  border: 0;
  outline: 0;
  font-size: 1rem;
  background: #fff;
  color: ${palette.primary['notBlack']};
  margin-left: 8px;
  width: ${size};
`

const labelStyle = css`
  padding-left: 13px;
  font-size: 1rem;

  display: flex;
  align-items: center;
  color: ${palette.primary['grey70']};
  pointer-events: none;
  position: absolute;
  transform: translate(0, 12px) scale(1);
  transform-origin: top left;
  transition: all 0.2s ease-out;
`
const clear = css`
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  padding-left: 0.5rem;
  cursor: pointer;
  color: ${palette.primary['grey70']};
  &:hover {
    color: ${palette.primary['notBlack']};
  }
`
const downArrow = (isActive: boolean, multiValue: boolean) => css`
  display: flex;
  align-items: center;
  font-size: 1.7rem;
  padding-left: 0.5rem;
  cursor: pointer;
  color: ${palette.primary['grey70']};
  &:hover {
    color: ${palette.primary['notBlack']};
  }
  ${isActive &&
  css`
    &:before {
      content: '';
      margin-right: 0.5rem;
      width: 1px;
      height: 1.5rem;
      background: ${palette.primary['grey30']};
    }
  `}

  ${multiValue &&
  css`
    &:before {
      content: '';
      margin-right: 0.5rem;
      width: 1px;
      height: 1.5rem;
      background: ${palette.primary['grey70']};
    }
  `}
`

const selectBlock = css`
  width: 100%;
  max-height: 172px;
  border-radius: 4px;
  background: #fff;
  box-shadow: 0 2px 8px 0px rgba(0, 0, 0, 0.2);
  margin-top: -4px;
  margin-bottom: 1rem;
  padding-top: 4px;
  padding-bottom: 4px;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    display: none;
  }
  scroll-padding-bottom: 4px;
  scroll-padding-top: 4px;
`
const selectItem = (select: boolean, selected?: boolean) => css`
  padding: 8px 18px;
  min-height: 3rem;
  display: flex;
  align-items: center;
  ${select &&
  css`
    background: ${palette.primary['grey10']};
  `}
  ${selected &&
  css`
    background: ${palette.primary['grey30']};
  `}
`
