import { css } from '@emotion/react'
import React, { useState } from 'react'
import ContentHeader from '../../foundations/Header/ContentHeader'
import palette from '../../lib/palette'
import Button from '../../foundations/Button/Button'
import Input from '../../foundations/Input/Input'
import Select from '../../foundations/Select/Select'

const locations = [
  '전국',
  '서울',
  '부산',
  '대구',
  '인천',
  '광주',
  '대전',
  '울산',
  '세종',
  '경기도',
]

export type CreateIndividualContactProps = {}

function CreateIndividualContact({}: CreateIndividualContactProps) {
  const [loading, setLoading] = useState(false)
  const [name, setName] = useState('')
  const [contact, setContact] = useState('')
  const [address, setAddress] = useState('')
  const [locationToUse, setLocationToUse] = useState('')
  const [locaions, setLocations] = useState([''])

  const handleChangeName = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target
    setName(value)
  }

  const handleChangeContact = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target
    const replaceValue = value
      .replace(/[^0-9]/g, '')
      .replace(
        /(^02|^0505|^1[0-9]{3}|^0[0-9]{2})([0-9]+)?([0-9]{4})$/,
        '$1-$2-$3'
      )
      .replace('--', '-')
    setContact(replaceValue)
  }

  const handleChangeAddress = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target
    setAddress(value)
  }

  const handleChangeLocationToUse = (
    e: React.ChangeEvent<HTMLInputElement>
  ) => {
    const { value } = e.target
    setLocationToUse(value)
  }

  const onSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      setLoading(true)
      console.log('Success')
    } catch (error) {
      console.log(error)
    } finally {
      console.log('개인 사용자 생성 완료')
      setLoading(false)
    }
  }

  return (
    <ContentHeader title="Create Individual Contact">
      <div css={block}>
        <div css={avatarCss}>
          <label>{name.slice(-2)}</label>
        </div>
        <form css={formCss} onSubmit={onSubmit}>
          <Input
            css={inputCss}
            label="이름"
            value={name}
            setValue={setName}
            onChange={handleChangeName}
            isClearable
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault()
              }
            }}
          />
          <Input
            css={inputCss}
            label="연락처"
            value={contact}
            setValue={setContact}
            onChange={handleChangeContact}
            isClearable
          />
          <Input
            css={inputCss}
            label="주소"
            value={address}
            setValue={setAddress}
            onChange={handleChangeAddress}
            isClearable
          />

          <Select
            css={inputCss}
            label="사용지역"
            value={locationToUse}
            setValue={setLocationToUse}
            onChange={handleChangeLocationToUse}
            onKeyDown={(e) => {
              if (e.key === 'Enter') {
                e.preventDefault()
              }
            }}
            options={locations}
            isMulti
          />

          <Button>{loading ? '로딩중' : 'Create Individual Contact'}</Button>
        </form>
      </div>
    </ContentHeader>
  )
}

export default CreateIndividualContact

const block = css`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
`

const avatarCss = css`
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background: ${palette.primary['grey70']};
  display: flex;
  margin-bottom: 2rem;
  label {
    width: 100%;
    color: #fff;
    font-size: 2rem;
    text-align: center;
    line-height: 5rem;
  }
`
const formCss = css`
  width: 100%;
`
const inputCss = css`
  margin-bottom: 1rem;
`
