import { css } from '@emotion/react'
import React, { useState } from 'react'
import Input from '../../../foundations/Input/Input'
import Button from '../../../foundations/Button/Button'
import useGoogleSignup from './../../../hooks/useGoogleSignup'

export type RegisterProps = {}

function Register({}: RegisterProps) {
  const [contact, setContact] = useState('')
  const { signup, loading, error } = useGoogleSignup()

  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target
    setContact(value)
  }

  const onSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      await signup(contact)
    } catch (e) {
      setContact('')
    }
  }

  return (
    <div css={block}>
      <form onSubmit={onSubmit}>
        <Input
          label="Contact"
          name="contact"
          value={contact}
          setValue={setContact}
          onChange={onChange}
        />
        <Button css={buttonStyle} disabled={loading}>
          {loading ? '로딩중' : 'Join Us!'}
        </Button>
      </form>
    </div>
  )
}

export default Register

const block = css`
  width: 100%;
`
const buttonStyle = css`
  margin-top: 2rem;
`
