import { css } from '@emotion/react'
import { sign } from 'crypto'
import React, { useCallback, useEffect, useRef } from 'react'
import { FcGoogle } from 'react-icons/fc'
import { checkGoogleResgistered } from '../../lib/api/user/checkGoogleRegistered'
import useGoogleSignin from './../../hooks/useGoogleSignin'

export type GoogleLoginButtonProps = {}

function GoogleLoginButton({}: GoogleLoginButtonProps) {
  const buttonRef = useRef<HTMLButtonElement>(null)
  const signin = useGoogleSignin()

  const onSuccess = useCallback(
    (googleUser: any) => {
      signin(googleUser.getAuthResponse(true).access_token)
    },
    [signin]
  )

  const onFailure = useCallback((e: any) => {}, [])

  useEffect(() => {
    window.gapi.load('auth2', function () {
      const auth2 = window.gapi.auth2.init({
        client_id:
          '617059692423-7tepraphoaqmocgvjuuu46h236ittdc8.apps.googleusercontent.com',
        cookiepolicy: 'single_host_origin',
      })
      auth2.attachClickHandler(buttonRef.current, {}, (googleUser: any) => {
        onSuccess(googleUser)
      })
    })
  }, [onSuccess, onFailure])

  return (
    // <div
    //   id="google-login-button"
    //   className="g-signin2"
    //   data-onsuccess="onSignIn"
    //   data-theme="dark"
    // ></div>
    <button css={googleStyle} ref={buttonRef} id="google-login-button">
      <FcGoogle />
    </button>
  )
}

export default GoogleLoginButton

const googleStyle = css`
  border: none;
  outline: none;
  background: none;
  padding: 0;
  text-decoration: none;
  cursor: pointer;

  svg {
    font-size: 1.75rem;
  }
`
