import React from 'react'
import { Route, Switch } from 'react-router-dom'
import WorkspacesNav from '../../components/Nav/WorkspacesNav'
import CreateIndividualContactPage from './CreateIndividualContactPage'
import { css } from '@emotion/react'

export type WorkspacesPageProps = {}

function WorkspacesPage({}: WorkspacesPageProps) {
  return (
    <div css={block}>
      <Switch>
        <Route exact path="/workspaces">
          <WorkspacesNav />
        </Route>
        <Route path="/workspaces/contact/create/individual">
          <CreateIndividualContactPage />
        </Route>
      </Switch>
    </div>
  )
}

export default WorkspacesPage

const block = css`
  height: 100%;
`
