import { useCallback } from 'react'
import { checkGoogleResgistered } from '../lib/api/user/checkGoogleRegistered'
import { googleSignin } from '../lib/api/user/googleSignin'
import { useHistory } from 'react-router-dom'
import { useSetRecoilState } from 'recoil'
import { googleTokenState } from '../atoms/userState'
import useAuth from './useAuthorize'

export default function useGoogleSignin() {
  const setGoogleToken = useSetRecoilState(googleTokenState)
  const { authorize } = useAuth()
  const history = useHistory()

  const signin = useCallback(
    async (accessToken: string) => {
      setGoogleToken(accessToken)
      try {
        const exists = await checkGoogleResgistered(accessToken)
        if (exists) {
          const { user } = await googleSignin(accessToken)
          authorize(user)
        } else {
          history.push('/register')
        }
      } catch (error) {
        // TODO: Error Dialog
      }
    },
    [history, setGoogleToken, authorize]
  )
  return signin
}
